import React,{Component } from 'react';
import Code from 'react-markdown';
import ResizeBox from './ResizeBox';
import {
    Layout,
    Menu,
    Icon,
    Timeline,
    Row,Col,
    Tooltip
} from 'antd';
const {
    Header, Footer, Sider, Content,
  } = Layout;
class  CssLayout extends Component {
    render(){
        return (
            <Layout style={{marginLeft:200}}>
                <Header style={{ background: '#fff', padding: 0,paddingLeft:20 ,fontSize:20,fontWeight:900}} >
                    <h2> CSS基本布局</h2> 
                </Header>
                <Content style={{ margin: '24px 16px 0', overflow: 'inherit'}}>
                <div style={{padding:24,backgroundColor:'#fff',height: ' calc(100vh - 160px)',overflowY:'auto'}}>
                    <Timeline>
                        <Timeline.Item>
                            <h3>自适应两栏布局</h3>
                            <p>利用BFC实现自适应两栏布局</p>
                            <Row style={{display:'flex'}}>
                                <Col span={12}>
                                   
                                        <Code source='
                                        ```js
                                            /****css****/
                                                .aside{
                                                float: left;
                                                width: 180px;
                                                height: 500px;
                                                background-color: red;
                                                opacity: 0.5;
                                                }
                                                .main{
                                                overflow: hidden;
                                                height: 500px;
                                                background-color: green;
                                                }

                                                <!--html-->
                                                <div class="box">
                                                <div class="aside">左侧</div>
                                                <div class="main">鼠标拖动移动右侧可改变大小</div>
                                                </div>
                                                 ```'>
                                        </Code>
                                  
                                </Col>
                                <Col span={12}>
                                    <ResizeBox>
                                        <div id="demo6">
                                                <div className="aside">左侧</div>
                                                <div className="main">鼠标拖动移动右侧可改变大小</div>
                                        </div>
                                    </ResizeBox>
                                </Col>
                            </Row>
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3>圣杯布局和双飞翼布局</h3>
                            <ol>
                                <li>解决什么问题：两边定宽，中间自适应的三栏布局，中间栏放在文档流前面以优先渲染</li>
                                <li>相同点：三栏全float浮动，左右两栏加上负margin让中间栏div 并排，形成三栏</li>
                                <li>不同点：
                                    <ul>
                                        <li>圣杯布局为了中间内容不被遮挡，将container设置左右padding-left和padding-right，将左右两个div用相对定位position:relative并分别配合right和left属性，以便左右两栏div移动后不会遮挡中间div。</li>
                                        <li>双飞翼布局，直接在中间main内部创建子div content用于放置内容，在content上设置margin-left和margin-right为左右两栏div留出位置。</li>
                                    </ul>
                                </li>
                                <li>
                                    <p>圣杯布局代码</p>
                                    <Row style={{display:'flex'}}>
                                        <Col span={12}>
                                            <Code source='
                                                ```js
                                                /****css****/
                                                .main{
                                                  width: 100%;
                                                  background: green;
                                                }
                                                .left{
                                                  left: -200px;
                                                  width: 200px;
                                                  margin-left: -100%;
                                                  background: yellowgreen;
                                                }
                                                .right{
                                                  right: -200px;
                                                  width: 200px;
                                                  margin-left: -200px;
                                                  background-color: red;
                                                }
                                                .main,.left,.right{
                                                  position: relative;
                                                  float: left;
                                                  min-height: 500px;
                                                }
                                                .container{
                                                  padding: 0 200px;
                                                  overflow: hidden;
                                                  border: 5px solid #ccc;
                                                }
                                                
                                                <!--html-->
                                                <div class="container">
                                                    <div class="main">中间栏目</div>
                                                    <div class="left">左侧栏目</div>
                                                    <div class="right">右侧栏目</div>
                                                </div> 
                                                ```
                                            '></Code>
                                        </Col>    
                                        <Col span={12}>
                                                <ResizeBox>
                                                    <div id="demo7" className="container">
                                                        <div className="main">中间栏目</div>
                                                        <div className="left">左侧栏目</div>
                                                        <div className="right">右侧栏目</div>
                                                    </div>
                                                </ResizeBox>
                                        </Col>    
                                    </Row>
                                </li>
                                <li>
                                    <p>双飞翼布局代码</p>
                                    <Row style={{display:'flex'}}>
                                        <Col span={12}>
                                            <Code source='
                                                ```js
                                                /****css****/
                                                .main,.left,.right{
                                                  float: left;
                                                  min-height: 500px;
                                                }
                                                .main{
                                                  width:100%;
                                                  background-color: red;
                                                }
                                                .left{
                                                  width: 200px;
                                                  margin-left: -100%;
                                                  background-color: yellow;
                                                }
                                                .right{
                                                  width: 200px;
                                                  background-color: green;
                                                  margin-left: -200px;
                                                }
                                                .content{
                                                  margin: 0 200px;
                                                }
                                                
                                                <!--html-->
                                                <div class="container"> 
                                                　　<div class="main">
                                                    　　<div class="content">中间栏目</div> 
                                                    </div>
                                                　　<div class="left">左侧栏目</div> 
                                                　　<div class="right">右侧栏目</div> 
                                                </div>       
                                                ```
                                            '></Code>
                                        </Col>
                                        <Col span={12}>
                                            <ResizeBox>
                                                <div id="demo8" className="container">
                                                    <div className="main">
                                                        <div className="content">中间栏目</div>
                                                    </div>
                                                    <div className="left">左侧栏目</div>
                                                    <div className="right">右侧栏目</div>
                                                </div>
                                            </ResizeBox>
                                        </Col>
                                    </Row>
                                </li>
                            </ol>
                        </Timeline.Item>
                        <Timeline.Item>
                            <h3></h3>
                        </Timeline.Item>
                    </Timeline>      
                </div>
                </Content>
                <Footer style={{ textAlign: 'center' }}>
                    This is my study note,welcome to comunicated with me , my wechat is "kannihappy"
                </Footer>
            </Layout>   
        )
    }
}
export default  CssLayout;